import React, { Component } from "react";
import PropTypes from "prop-types";
import { SectionProps } from "../../utils/SectionProps";

const propTypes = {
  ...SectionProps.types,
  split: PropTypes.bool,
};

const defaultProps = {
  ...SectionProps.defaults,
  split: false,
};

class PostTribute extends Component {
  constructor() {
    super();
    this.state = {
      personName: "",
      personTribute: "",
    };
  }

  onNameChange = (event) => {
    this.setState({ personName: event.target.value });
  };

  onTributeChange = (event) => {
    this.setState({ personTribute: event.target.value });
  };

  onSubmitTribute = () => {
    fetch("https://fierce-tundra-85866.herokuapp.com/submit", {
      method: "post",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        name: this.state.personName,
        body: this.state.personTribute,
      }),
    });
    Array.from(document.querySelectorAll(".clear-me")).forEach(
      (input) => (input.value = "")
    );
    this.setState({
      itemvalues: [{}],
    });
    alert("Tribute submitted");
    window.location.reload();
  };

  render() {
    return (
      <section className="has-bottom-divider has-top-divider cta section center-content-mobile reveal-from-bottom">
        <div className="container">
          <div className="cta-inner section-inner">
            <div className="cta-slogan">
              <h3 className="m-0">Post Your Tributes</h3>
            </div>

            <div className="post-tribute-action">
              <input
                onChange={this.onNameChange}
                className="name-box clear-me"
                id="tribute-post"
                type="text"
                label="Name"
                hasIcon="right"
                placeholder="Your full name"
              ></input>
              <br></br>
              <textarea
                onChange={this.onTributeChange}
                className="text-box clear-me"
                type="text"
                label="Tribute"
                rows="5"
                cols="200"
                placeholder="Post your tributes"
                id="comment_content tribute-post"
              ></textarea>
              <br></br>
              <button
                onClick={this.onSubmitTribute}
                className="font-bold py-2 px-4 w-full bg-purple-400 text-lg text-white shadow-md rounded-lg button button-light button-wide-mobile button-sm"
              >
                Post
              </button>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

PostTribute.propTypes = propTypes;
PostTribute.defaultProps = defaultProps;

export default PostTribute;
