import React, { Component } from "react";
import CardList from "../elements/CardList";

class Tributes extends Component {
  constructor() {
    super();
    this.state = {
      posts: [],
    };
  }

  componentDidMount() {
    fetch("https://fierce-tundra-85866.herokuapp.com/")
      .then((response) => response.json())
      .then((posts) => {
        this.setState({ posts });
      })
      .catch((e) => console.log(e));
  }

  render() {
    return (
      <section className="container center-content" id="tributes">
        <h2>Tributes</h2>
        <CardList posts={this.state.posts} />
      </section>
    );
  }
}

export default Tributes;
