import React from "react";
import Card from "../elements/Card";

const CardList = ({ posts }) => {
  return (
    <div>
      {posts &&
        posts.map((tribute) => {
          return (
            <Card key={tribute._id} name={tribute.name} body={tribute.body} />
          );
        })}
    </div>
  );
};

export default CardList;
